import React from 'react';
import {Link} from 'react-router-dom'
const Header=(props)=> {
  return (
    <nav className="navbar navbar-expand-sm navbar-dark bg-success mb-3 py-0">
        <div className="container">
        <a href="/" className='navbar-brand'><i className="fa-solid fa-user"></i>   {props.branding}</a>
        <div>
            <ul className='navbar-nav mr-auto'>
                <li className='nav-item'>
                    <Link to="/" className='nav-link'>
                    <i className="fa-solid fa-house"></i>Home</Link>
                </li>
                <li className='nav-item'>
                    <Link to="/contact/add" className='nav-link'>
                    <i className="fa-solid fa-plus"></i>Add</Link>
                </li>
                <li className='nav-item'>
                    <Link to="/about" className='nav-link'>
                    <i className="fa-regular fa-circle-question"></i>About</Link>
                </li>
            </ul>
        </div>
        </div>
    </nav>
  )
}
export default Header; 
